import styled from "styled-components";
import { useNavigate } from "react-router-dom";

import { FlexContent, TextS } from "../../../styles/Component";

const Profile = () => {
    const AdminName = sessionStorage.getItem("token");
    const navigate = useNavigate();

    const handleLogout = () => {
		sessionStorage.removeItem('token');

		navigate('/');
    };

    return (
        <>
            <FlexContent>
                <TextS $padding="0 2em">, {AdminName} </TextS>
                <LogoutBtn $underline="underline" fontWeight="bold" onClick={handleLogout}></LogoutBtn>
            </FlexContent>
        </>
    );
};

const LogoutBtn = styled.button`
    display : flex;
    justify-content : center;
    align-items : center;
	line-height: 18px;
    padding: 0;
    color: #464849 ;
	font-size: 14px;
	font-weight: bold;
    text-decoration: underline;
`;

export default Profile;